// scripts/dropdown.js


const dropdowns = document.querySelectorAll(".dropdown-content");
const subDropdowns = document.querySelectorAll(".dropdown-subcontent");

function positionDropdown(relativeElement, dropdown) {
    const btnRect = relativeElement.getBoundingClientRect();
    console.log(relativeElement, dropdown)
    dropdown.style.position = "absolute";

    // Place dropdown directly below relativeElement
    dropdown.style.top = `${window.scrollY + btnRect.bottom + border}px`;
    dropdown.style.left = `${window.scrollX + btnRect.left}px`;

    // Update dropRect AFTER positioning
    const dropRect = dropdown.getBoundingClientRect();

    // Keep dropdown inside viewport horizontally
    if (dropRect.right > window.innerWidth - border) {
        dropdown.style.left = `${window.innerWidth - dropRect.width - border}px`;
    } else if (dropRect.left < border) {
        dropdown.style.left = `${border}px`;
    }

    // Optional: keep dropdown visible vertically
    if (dropRect.bottom > window.innerHeight - border) {
        dropdown.style.top = `${window.scrollY + btnRect.top - dropRect.height - border}px`; // show above relativeElement
    }
}

function positionSubdropdown(relativeElement, dropdown) {
    const elementRect = relativeElement.getBoundingClientRect();

    dropdown.style.position = "absolute";

    const dropRect = dropdown.getBoundingClientRect();

    let top = window.scrollY + elementRect.bottom + border - dropRect.height
    if (top < 0) {
        top -= top
    }
    dropdown.style.top = `${top}px`;
    dropdown.style.left = `${window.scrollX + elementRect.left + dropRect.width - border * 3}px`;
}

function wireDropdown(dropdown) {
    const buttonId = dropdown.dataset.toggle;
    const button = document.getElementById(buttonId);

    if (!button) return;

    button.addEventListener("click", () => {
        if (dropdown.classList.contains("dropdown-content")) {
            // top-level dropdown
            positionDropdown(button, dropdown);
        } else {
            // subdropdown
            positionSubdropdown(button, dropdown);
        }
    });

    // handle each sub-dropdown trigger (children that open subcontent)
    const subTriggers = dropdown.querySelectorAll(".dropdown-subcontent-option");

    for (let trigger of subTriggers) {
        const subdropdown = document.querySelector(
            `.dropdown-subcontent[data-toggle="${trigger.id}"]`
        );
        if (!subdropdown) continue; // skip if no subdropdown

        // add chevron if not already added
        if (!trigger.querySelector(".subcontent-chevron")) {
            const chevron = document.createElement("i");
            chevron.className = "fa-solid fa-chevron-right subcontent-chevron";
            trigger.appendChild(chevron);
        }

        // Position on resize
        window.addEventListener("resize", () => {
            positionSubdropdown(trigger, subdropdown);
        });

        // Hover to show
        trigger.addEventListener("mouseenter", () => {
            positionSubdropdown(trigger, subdropdown);
            subdropdown.classList.add("show");
        });

        trigger.addEventListener("mouseleave", (e) => {
            if (
                !subdropdown.classList.contains("click-show") &&
                !subdropdown.contains(e.relatedTarget) &&
                !trigger.contains(e.relatedTarget)
            ) {
                subdropdown.classList.remove("show");
            }
        });

        // Click to toggle persistent show
        trigger.addEventListener("click", (e) => {
            e.preventDefault();
            subdropdown.classList.toggle("click-show");
        });

        // Recurse into this subdropdown so its own children get wired up
        wireDropdown(subdropdown);
    }

    // Top-level button only (for .dropdown-content)
    if (button) {
        button.addEventListener("click", () => {
            dropdown.classList.toggle("show");
        });
    }
}

function updateDropdowns() {
    // Wire every dropdown (both .dropdown-content and .dropdown-subcontent)
    const allDropdowns = document.querySelectorAll(
        ".dropdown-content, .dropdown-subcontent"
    );
    for (let dropdown of allDropdowns) {
        wireDropdown(dropdown);
    }
}

function setupDropdowns() {
    const allDropdowns = document.querySelectorAll(".dropdown-content, .dropdown-subcontent.show, .dropdown-subcontent.click-show");

    for (let dropdown of allDropdowns) {
        const toggleId = dropdown.dataset.toggle;
        const trigger = document.getElementById(toggleId);

        if (!trigger) continue;

        trigger.addEventListener("click", () => {
            if (dropdown.classList.contains("dropdown-content")) {
                // top-level dropdown
                positionDropdown(trigger, dropdown);
            } else {
                // subdropdown
                positionSubdropdown(trigger, dropdown);
            }
        });
    }
}